*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #e6e6e6;
}

.template {
  width: 45rem;
  background-color: #fff;
  margin: auto;
  margin-top: 10rem;
  border-radius: 5px;
  overflow: hidden;
  
}

.title {
  height: 8rem;
  background-color: #cdb4db;
  color: #fff;
  font-size: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  

}

.reset-btn {
  
 
  border: none;
  /* padding: 1remrem; */
  left: 73.3%;
  top: -60%;
  height: 3rem;
  position: absolute;
  width: 12rem;
  border-top-left-radius: 2px;
  cursor: pointer;

}

.reset-btn:hover {
  color: #fff;
  background-color: #888;
  
}
/* Input Section */

.input-wrapper {
  position: relative;
  height: 5rem;
  display: flex;
 
}

.input-box {
  width: 86%;
}

.button-box {
  width: 14%;
}

.search-input {
  width: 100%;
  height: 100%;
  outline: none;
  font-size: 2rem;
  border: none;
  padding-left: 1rem;
  background-color: #565656;
  color: #fff;
}

input::placeholder {
  color: #fff;
}

.add-button {
  width: 100%;
  height: 100%;
  background-color: #999;
  border: none;
  color: #fff;
  font-size: 3rem;
  cursor: pointer;
}

/* TodoList Styling */

.todolist-wrapper {
  height: 470px;
  overflow-x: hidden;
}

.todolist {
  height: 12.5%;
  background-color: #fff;
  border-top: .5px solid rgb(212, 212, 212);
  display: flex;
  padding-left: 2rem;
  align-items: center;
}

.todolist:nth-child(even) {
  background-color: #f1f1f1;
}

.hover {
  opacity: .6;
}

.checkBox-icon {
  font-size: 2rem;
  margin-right: 1rem;
  cursor: pointer;
}

.todo-textbox {
  font-size: 1.6rem;
  
  width: 65%;
}

.fuction-icon-box {
  font-size: 1.8rem;
  width: 10rem;
  display: flex;
  justify-content: space-around;
}

.modify-btn {
  margin-left: 2rem;
  cursor: pointer;
}

.delete-btn {
  cursor: pointer;
}

.modify-btn:hover {
  opacity: .6;
}

.delete-btn:hover {
  opacity: .6;
}

.text-checked {
  opacity: .6;
  text-decoration: line-through;
}

.modify-wrapper {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0
}

.modify-input-box {
  width: 30rem;
  height: 20rem;
  background-color: #fff;
  margin: auto;
  margin-top: 35rem;
}

.modify-input-title {
  height: 23%;
  background-color: #cdb4db;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 2.5rem;
  font-weight: 650;
}

.modify-input {
  margin: auto;
  display: block;
  margin-top: 5rem;
  border: none;
  border-bottom: 1.2px solid #cdb4db;
  padding: .5rem .5rem;
  outline: none;

}

.modify-input-btn {
  margin: auto;
  display: block;
  margin-top: 2rem;
  background-color: #cdb4db;
  border: none;
  padding: 1rem 2rem;
  color: #fff;
  cursor: pointer;
}

.modify-input-btn:hover {
  background-color: #b98dd3;
}

/* Scroll bar Styling */

.todolist-wrapper::-webkit-scrollbar {
  width: 10px;  /* 스크롤바의 너비 */
  position: absolute;

}

.todolist-wrapper::-webkit-scrollbar-thumb {
  height: 10%; 
  background: #cdb4db; 
  border-radius: 10px;
}

/* reset alert */

.reset-wrapper {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.reset-alert-box {
  width: 30rem;
  height: 20rem;
  background-color: #fff;
  margin: auto;
  margin-top: 35rem;
  text-align: center;
  position: relative;
}

.reset-alert-title {
  height: 23%;
  background-color: #cdb4db;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 2.5rem;
  font-weight: 650;
  
}

.reset-text {
  font-size: 1.3rem;
  margin-top: 3rem;
}

.reset-button-box {
  height: 3.2rem;
  
  margin-top: 6rem;
  display: flex;
  justify-content: space-around;
}

.reset-button-box button {
  width: 40%;
  border: none;
  height: 80%;
  cursor: pointer;
  color: #fff;
  background-color: #cdb4db;
  border-radius: 3px;
  font-weight: 600;
}


.reset-button-box button:hover {
  background-color: black;
}


